.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 50vw;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: calc(50% - 25vw);
    top: 19%;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}

@media (max-width: 1200px) {
    .Modal {
        width: 70%;
        left: 15%;
    }
}

@media (max-width: 450px) {
    .Modal {
        width: 80%;
        left: 10%;
        top: 10%;
        height: 85vh;
    }
}